import { USER_LOGIN_FAIL, USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_LOGOUT, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS, USER_DETAILS_FAIL, USER_DETAILS_SUCCESS, USER_DETAILS_REQUEST, USER_UPDATE_REQUEST, USER_UPDATE_SUCCESS, USER_UPDATE_FAIL, USER_UPDATE_RESET, USER_ORDERS_REQUEST, USER_ORDERS_SUCCESS, USER_ORDERS_FAIL, USER_ORDERS_RESET, USER_DETAILS_RESET } from '../constants/userConstants'

export const userLoginReducer = (state={  }, action) => {
    switch(action.type) {
        case USER_LOGIN_REQUEST:
            return { loading: true }
        case USER_LOGIN_SUCCESS:
            return { loading: false, userInfo: action.payload }
        case USER_LOGIN_FAIL:
            return { loading: false, error: action.payload }
        case USER_LOGOUT:
            return {}
        default:
            return state
    }
}

export const userRegisterReducer = (state={  }, action) => {
    switch(action.type) {
        case USER_REGISTER_REQUEST:
            return { loading: true }
        case USER_REGISTER_SUCCESS:
            return { loading: false, userInfo: action.payload }
        case USER_REGISTER_FAIL:
            return { loading: false, error: action.payload }
        default:
            return state
    }
}

export const userDetailsReducer = (state={ user: {} }, action) => {
    switch(action.type) {
        case USER_DETAILS_REQUEST:
            return { ...state, loading: true }
        case USER_DETAILS_SUCCESS:
            return { loading: false, user: action.payload }
        case USER_DETAILS_FAIL:
            return { loading: false, error: action.payload }
        case USER_DETAILS_RESET:
            return { user: {} }
        default:
            return state
    }
}

export const userUpdateReducer = (state={ user: {} }, action) => {
    switch(action.type) {
        case USER_UPDATE_REQUEST:
            return { ...state, loading: true }
        case USER_UPDATE_SUCCESS:
            return { loading: false, success: true, userInfo: action.payload }
        case USER_UPDATE_FAIL:
            return { loading: false, error: action.payload }
        case USER_UPDATE_RESET:
            return { loading: false, success: true, userInfo: action.payload }
        default:
            return state
    }
}

export const userOrdersReducer = (state={ orders: [] }, action) => {
    switch(action.type) {
        case USER_ORDERS_REQUEST:
            return { ...state, loading: true }
        case USER_ORDERS_SUCCESS:
            return { loading: false, orders: action.payload }
        case USER_ORDERS_FAIL:
            return { loading: false, error: action.payload }
        case USER_ORDERS_RESET:
            return { orders: [] }
        default:
            return state
    }
}
